a {
    font-size: medium;
    text-decoration: none;
    white-space: nowrap;

    span {
        color: var(--secondary-color)
    }
}

.app__navbar {
    width: 100%;

    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 1rem 2rem;
    background: rgba(0, 0, 0, 0.25);

    backdrop-filter: blur(4px);
    -webkit-backdrop-filter: blur(4px);
    border: 1px solid rgba(255, 255, 255, 0.18);
    position: fixed;
    z-index: 2;
}

.app__navbar-logo {
    display: flex;
    justify-content: flex-start;
    align-items: center;

    img {
        width: 120px;
        height: 40px;

        @media screen and (min-width: 2000px) {
            width: 180px;
            height: 60px;
        }
    }
}

.app__navbar-links {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;

    list-style: none;

    li {
        margin: 0 1rem;
        cursor: pointer;

        flex-direction: column;

        div {
            width: 5px;
            height: 5px;
            background: transparent;
            border-radius: 50%;

            margin-bottom: 5px;
        }

        a {
            color: var(--white-color);
            text-decoration: none;
            flex-direction: column;

            text-transform: uppercase;
            font-weight: 800;

            transition: all 0.3s ease-in-out;

            &:hover {
                color: var(--secondary-color);
            }
        }

        &:hover {
            div {
                background: var(--secondary-color);
            }
        }
    }

    @media screen and (max-width: 1000px) {
        display: none;
    }
}

.app__navbar-menu {
    height: 35px;
    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;


    svg {
        width: 100%;
        height: 100%;
        color: var(--white-color);
    }

    div {
        position: fixed;
        top: 0;
        bottom: 0;
        right: 0;
        z-index: 5;

        padding: 1rem;

        width: 70vw;
        height: 100vh;

        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
        flex-direction: column;
        gap: 0;

        background: var(--background-darken), url(../../assets/bg39.jpg);
        background-size: cover;
        background-repeat: repeat;

        /* top box shadow */
        box-shadow: 0px 0px 20px rgba(168, 168, 168, 0.15);

        div {
            background: none;
            box-shadow: none;

            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            gap: 2rem;

            height: 20vh;

            

            a {
                width: 35px;
                height: 35px;
                svg {
                    width: 100%;
                    height: 100%;
                    color: var(--white-color);
                    margin: 0.5rem 1rem;
                }
            }
        }

        ul {
            list-style: none;
            margin: 0;
            padding: 0;
            height: 80%;
            width: 100%;

            display: flex;
            justify-content: flex-start;
            align-items: flex-start;
            flex-direction: column;

            li {
                margin: 1rem;

                a {
                    color: var(--white-color);
                    text-decoration: none;
                    font-size: 1rem;
                    text-transform: uppercase;
                    font-weight: 800;

                    transition: all 0.3s ease-in-out;

                    &:hover {
                        color: var(--secondary-color);
                    }
                }
            }
        }


        @media screen and (max-width: 450px) {
            gap:1rem;
            width: 100%;
        }

        @media screen and (min-width: 1000px) {
            display: none;
        }
    }

    @media screen and (min-width: 1000px) {
        display: none;
    }
}